import SimpleLightbox from "simplelightbox";
import "simplelightbox/dist/simple-lightbox.min.css";



const item = document.querySelector('.gallery');

item.innerHTML = images.map(({ preview, original, description }) => `
  <li class="gallery-item">
    <a class="gallery-link" href="${original}">
      <img class="gallery-image" src="${preview}" alt="${description}" width="360" height="200" />
    </a>
  </li>
`).join('');


let gallery = new SimpleLightbox('.gallery a', {
    captionDelay: 250, captionsData: 'alt', className: 'modal-style'});
gallery.on('show.simplelightbox', function () {
});









